export default {
	data() {
		return {
			nodes:`<div><span>富文本内容</span><span style="color:red;">初始HTML</span></div>`,//富文本内容
			contactArr:[
				{
					name:'赵某某',phone:'15154546565',
				},{
					name:'钱某某',phone:'15154546565',
				},{
					name:'孙某某',phone:'15154546565',
				},
			],
			form: {
				additionalProp1: '0',
				additionalProp2: '0',
				additionalProp3: '0',
				additionalProp4: '0'
			},
			list1: [
				{
					value: '0',
					name: '被盗',
					checked: true
				},
				{
					value: '1',
					name: '人身伤害'
				},
				{
					value: '2',
					name: '大量聚集'
				}
			],
			list2: [
				{
					value: '0',
					name: '红'
				},
				{
					value: '1',
					name: '橙'
				},
				{
					value: '2',
					name: '黄'
				},
				{
					value: '3',
					name: '蓝'
				}
			],
			list3: [
				{
					value: '0',
					name: '本地居家隔离'
				},
				{
					value: '1',
					name: '返程后居家隔离'
				},
				{
					value: '2',
					name: '封路暂时不能返程'
				},
				{
					value: '3',
					name: '其余原因不能返程'
				}
			],
			list4: [
				{
					value: '0',
					name: '安全'
				},
				{
					value: '1',
					name: '低风险'
				},
				{
					value: '2',
					name: '高风险'
				},
				{
					value: '3',
					name: '未知'
				}
			]
		};
	},
	onLoad(e) {},
	methods: {
		handleRadioChange(evt, name) {
			this.form[name] = evt.target.value;
		},
		send() {
			let params = JSON.stringify({
				userid: '123',
				results: this.form
			});
			this.$HTTP({
				url: '/campusapp/userhealth/answer',
				root: 'http://61.132.95.169:10105',
				params,
				successCallback: res => {
					console.log(res, 'res');
					uni.showToast({
						title: '提交成功!'
					});
				}
			});
		}
	}
};
